'use client';

import React, { createContext, useState, useEffect, useCallback } from 'react';
import {
  onAuthStateChanged,
  signOut as firebaseSignOut,
  type User as FirebaseUser,
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  updateProfile,
} from 'firebase/auth';
import {
  doc,
  getDoc,
  setDoc,
  updateDoc,
  increment,
  collection,
  addDoc,
  Timestamp,
  query,
  where,
  onSnapshot,
  orderBy,
  getDocs,
} from 'firebase/firestore';
import { auth, db } from '@/lib/firebase';
import type { AuthContextType, UserData, WithdrawalRequest } from '@/lib/types';
import { useToast } from '@/hooks/use-toast';

export const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState<FirebaseUser | null>(null);
  const [userData, setUserData] = useState<UserData | null>(null);
  const [loading, setLoading] = useState(true);
  const [userWithdrawals, setUserWithdrawals] = useState<WithdrawalRequest[]>([]);
  const [allWithdrawals, setAllWithdrawals] = useState<WithdrawalRequest[]>([]);
  const { toast } = useToast();

  const handleUser = useCallback(async (firebaseUser: FirebaseUser | null) => {
    if (firebaseUser) {
      setUser(firebaseUser);
      const userRef = doc(db, 'users', firebaseUser.uid);
      const userSnap = await getDoc(userRef);
      if (userSnap.exists()) {
        const data = userSnap.data() as UserData;
        setUserData(data);
      } else {
        // This case handles user creation
        const newUserData: UserData = {
          uid: firebaseUser.uid,
          email: firebaseUser.email,
          name: firebaseUser.displayName || firebaseUser.email?.split('@')[0] || 'New User',
          balance: 0,
        };
        await setDoc(userRef, newUserData);
        setUserData(newUserData);
      }
    } else {
      setUser(null);
      setUserData(null);
    }
    setLoading(false);
  }, []);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
        setLoading(true);
        handleUser(user);
    });
    return () => unsubscribe();
  }, [handleUser]);

  useEffect(() => {
    if (!user) {
      setUserWithdrawals([]);
      return;
    }
    // Removed orderBy to avoid needing a composite index. Sorting is now done on the client.
    const q = query(
      collection(db, 'withdrawals'),
      where('userId', '==', user.uid)
    );
    const unsubscribe = onSnapshot(q, (querySnapshot) => {
      const withdrawals = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
        createdAt: (doc.data().createdAt as Timestamp).toDate(),
      })) as WithdrawalRequest[];
      // Sort withdrawals by date on the client side
      withdrawals.sort((a, b) => b.createdAt.getTime() - a.createdAt.getTime());
      setUserWithdrawals(withdrawals);
    }, (error) => {
       console.error("Error fetching user withdrawals:", error);
       toast({
         variant: 'destructive',
         title: 'Database Error',
         description: 'Could not load withdrawal history. Please try again later.',
       });
    });
    return () => unsubscribe();
  }, [user, toast]);


  // For admin
  useEffect(() => {
    if (!user) {
        setAllWithdrawals([]);
        return;
    }
    const adminEmail = process.env.NEXT_PUBLIC_ADMIN_EMAIL || process.env.ADMIN_EMAIL;
     if (user && adminEmail === user.email) {
      const q = query(collection(db, 'withdrawals'), orderBy('createdAt', 'desc'));
      const unsubscribe = onSnapshot(q, (querySnapshot) => {
        const withdrawals = querySnapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
          createdAt: (doc.data().createdAt as Timestamp).toDate(),
        })) as WithdrawalRequest[];
        setAllWithdrawals(withdrawals);
      }, (error) => {
        if (error.code !== 'permission-denied' && error.code !== 'failed-precondition') {
          console.error("Error fetching all withdrawals:", error);
        }
         if (error.code === 'failed-precondition') {
           console.error("Firestore index missing for admin query:", error.message);
            toast({
              variant: 'destructive',
              title: 'Admin Error: Index Required',
              description: 'An index is required for sorting all withdrawals. Please create it in the Firebase console.',
            });
         }
      });
      return () => unsubscribe();
    }
  }, [user, toast]);

  const signUpWithEmail = async (email: string, password: string) => {
    setLoading(true);
    try {
      const userCredential = await createUserWithEmailAndPassword(auth, email, password);
      const name = email.split('@')[0];
      await updateProfile(userCredential.user, { displayName: name });
      // handleUser will be called by onAuthStateChanged
      toast({ title: 'Success', description: 'Account created successfully!' });
      return true;
    } catch (error: any) {
      console.error('Error signing up', error);
      toast({ variant: 'destructive', title: 'Sign-up Error', description: error.message });
      setLoading(false);
      return false;
    }
  };
  
  const signInWithEmail = async (email: string, password: string) => {
    setLoading(true);
    try {
      await signInWithEmailAndPassword(auth, email, password);
      // handleUser will be called by onAuthStateChanged
      return true;
    } catch (error: any) {
      console.error('Error signing in', error);
      toast({ variant: 'destructive', title: 'Sign-in Error', description: error.message });
      setLoading(false);
      return false;
    }
  };


  const signOut = async () => {
    setLoading(true);
    try {
      await firebaseSignOut(auth);
    } catch (error) {
       console.error('Error signing out', error);
       toast({ variant: 'destructive', title: 'Sign-out Error', description: 'Could not sign out.' });
    } finally {
        setLoading(false);
    }
  };

  const updateBalance = useCallback(async (amount: number) => {
    if (!user) return;
    const userRef = doc(db, 'users', user.uid);
    await updateDoc(userRef, { balance: increment(amount) });
    setUserData((prev) => (prev ? { ...prev, balance: prev.balance + amount } : null));
  }, [user]);

  useEffect(() => {
    if (!user) return;
    const interval = setInterval(() => {
      updateBalance(0.20);
    }, 60000); // 60 seconds
    return () => clearInterval(interval);
  }, [user, updateBalance]);

  const requestWithdrawal = async (amount: number, gamepassLink: string) => {
    if (!user || !userData || userData.balance < amount) {
      toast({ variant: 'destructive', title: 'Withdrawal Error', description: 'Insufficient balance or not signed in.' });
      return false;
    }
    try {
      const withdrawalRequest = {
        userId: user.uid,
        userEmail: user.email,
        amount,
        gamepassLink,
        status: 'pending' as const,
        createdAt: new Timestamp(Math.floor(Date.now() / 1000), 0),
      };
      await addDoc(collection(db, 'withdrawals'), withdrawalRequest);
      await updateBalance(-amount);
      toast({ title: 'Success', description: 'Withdrawal request submitted.' });
      return true;
    } catch (error) {
      console.error('Error requesting withdrawal:', error);
      toast({ variant: 'destructive', title: 'Server Error', description: 'Could not process your request.' });
      return false;
    }
  };
  
  const updateWithdrawalStatus = async (id: string, status: 'completed' | 'rejected') => {
    const withdrawalRef = doc(db, 'withdrawals', id);
    try {
      await updateDoc(withdrawalRef, { status });
      toast({ title: 'Success', description: `Withdrawal marked as ${status}.` });

      if (status === 'rejected') {
        const withdrawalSnap = await getDoc(withdrawalRef);
        if (withdrawalSnap.exists()) {
          const { userId, amount } = withdrawalSnap.data();
          const userRef = doc(db, 'users', userId);
          await updateDoc(userRef, { balance: increment(amount) });
        }
      }

    } catch (error) {
      console.error('Error updating status:', error);
      toast({ variant: 'destructive', title: 'Update Error', description: 'Could not update withdrawal status.' });
    }
  };

  const value = {
    user,
    userData,
    loading,
    signUpWithEmail,
    signInWithEmail,
    signOut,
    requestWithdrawal,
    updateWithdrawalStatus,
    userWithdrawals,
    allWithdrawals
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}
